import '../oweek.css';

const Coundown = () => {

    var today = new Date();
    var day = today.getDay();
    var month = today.getMonth();
    var year = today.getFullYear();

    return (
        <div>
            <h2>{day}</h2>
            <h2>{month}</h2>
            <h2>{year}</h2>
            <h2>{today.toDateString()}</h2>
        </div>
    )
}

const Homepage = () => (
    <div className='oweek-page'>
        <h1 className='oweek-welcome'>Welcome to McMurtry!</h1>
        <Coundown/>
    </div>
)

export default Homepage;